import React from 'react';
import './Formulario.css'
import axios from 'axios'
import SimpleReactValidator from 'simple-react-validator'

// Imagenes
import iconocheck from '../../assets/img/generales/iconocheckok.png'
import iconoerror from '../../assets/img/generales/iconoerror.png'
// import iconowa from '../../assets/img/generales/iconowa.png'
import mensaje from '../../assets/img/generales/mensaje.png'
// import horario_to from '../../assets/img/generales/horariodeatencionwa.png'
import botonayudacodigo from '../../assets/img/generales/botonayudacodigo.png'

class Formulario2 extends React.Component {

    codigoRef = React.createRef();
    operatorRef = React.createRef();

    state = {
        codigo: {
            code: this.props.numero,
            operator: this.props.operator
        },
        status: null,
        codefinal: ''
    }

    constructor(props) {
        super(props);
        // Se carga el objeto para validar el formulario
        this.validator = new SimpleReactValidator({
            messages: {
                required: 'Este campo es obligatorio',
                min: 'El código debe de tener al menos 16 dígitos'
            }
        });
    }

    componentDidMount() {
        this.getLocation();
    }

    getLocation = async () => {
        await axios.get('https://ipapi.co/json/')
            .then(res => {

                this.setState({
                    "ip": res.data.ip
                })
            })

    }

    changeState = () => {
        this.setState({
            codigo: {
                "mayor_de_edad": "Sí",
                "ip": this.state.ip,
                "eventId": "-MLKB-tNAxfOLRiJ3cmZ",
                "code": this.codigoRef.current.value,
                "operator": this.operatorRef.current.value
            }
        })
    }

    ingresarCodigo = async (e) => {
        e.preventDefault();

        // Rellenar el state con formulario
        this.changeState();

        if (this.validator.allValid()) {
            this.setState({
                status: 'waiting'
            })
            // Petición http por post para el código
            await axios.post('https://api-v5.rebustech.io/custom/validate-token-to-estereo-picnic-movistar', this.state.codigo, {
                headers: {
                    'auth-token': 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJjbGllbnRJZCI6Ii1NS3BqWk0xSTVsalNqYnFqcDQ3In0.GHGAyYxGIgJtx9fGiwsh97sq_nxwblMTzuf6RoJ81BU'
                }
            })
                .then(res => {
                    this.setState({
                        message: res.data.message,
                        codefinal: this.codigoRef.current.value,
                        status: 'success'
                    })
                })
                .catch(err => {
                    console.log(err.response.data.code)
                    if (err.response.data.code === "M03") {
                        this.setState({
                            message: "Tu código está mal escrito o es inválido. ¡Por favor inténtalo nuevamente!",
                            codefinal: this.codigoRef.current.value,
                            status: 'failed',
                            codigo: {
                                "code": '',
                            }
                        })
                    }

                    if (err.response.data.code === "M01") {
                        this.setState({
                            message: "Tu código está mal escrito o es inválido. ¡Por favor inténtalo nuevamente!",
                            codefinal: this.codigoRef.current.value,
                            status: 'retry',
                        })
                    }

                    if (err.response.data.code === "M05") {
                        this.setState({
                            message: 'Tu código ya ha sido validado anteriormente. Si no fuiste tú, por favor contacta a soporte haciendo clic en el botón "?".',
                            codefinal: this.codigoRef.current.value,
                            status: 'failed',
                            codigo: {
                                "code": '',
                            }
                        })
                    }


                    if (err.response.data.code === "M07") {
                        this.setState({
                            message: 'El código ingresado se encuentra vencido. No puede ser validado, por favor contacta a soporte haciendo clic en el botón "?".',
                            codefinal: this.codigoRef.current.value,
                            status: 'failed',
                            codigo: {
                                "code": '',
                            }
                        })
                    }


                    if (err.response.data.code === "M77") {
                        this.setState({
                            message: "Máximo de peticiones erróneas permitidas en este dispositivo por seguridad. Por favor inténtalo de nuevo en unos minutos",
                            codefinal: this.codigoRef.current.value,
                            status: 'failed',
                            codigo: {
                                "code": '',
                            }
                        })
                    }

                })
        } else {
            this.validator.showMessages();
            this.forceUpdate()
            this.setState({
                status: 'No success'
            })
        }
    }

    returnForm = () => {

        const failed_form = document.querySelector('.failed_form');
        const realForm = document.querySelector('#realForm');
        realForm.classList.remove('fadeOut');
        failed_form.classList.remove('fadeIn_form');
        this.setState({
            status: 'return'
        })
    }

    showWhatsapp = () => {
        const mensaje_ws = document.querySelector('.mensaje');
        mensaje_ws.classList.toggle('mensaje_show');

    }

    render() {

        const realForm = document.querySelector('#realForm');
        const success_form = document.querySelector('.success_form');
        const failed_form = document.querySelector('.failed_form');
        const mensaje_ws = document.querySelector('.mensaje');


        if (this.state.status === 'success') {
            realForm.classList.add('fadeOut')
            success_form.classList.add('fadeIn_form')
        }

        if (this.state.status === 'failed') {
            realForm.classList.add('fadeOut')
            failed_form.classList.add('fadeIn_form')
        }

        if (this.state.status === 'return') {
            mensaje_ws.classList.add('heartbeat')
        }

        if (this.state.status === 'waiting') {
            mensaje_ws.classList.remove('heartbeat')

        }

        return (
            <section className="formulario">
                <div className="form">
                    <form id={"realForm"} onSubmit={this.ingresarCodigo}>
                        <div>
                            {/* <label htmlFor="">EN MANTENIMIENTO</label> */}

                            <label htmlFor="codigo">INGRESA TU CÓDIGO DE ACCESO</label> <br />
                            <input type="text" name="codigo" ref={this.codigoRef} onChange={this.changeState} value={this.state.codigo.code} />

                            {this.validator.message('codigo', this.state.codigo.code, 'required|min:16')}
                            <div>
                                {this.state.status === 'waiting' &&
                                    <p className="cargando">Cargando...</p>
                                }

                                {this.state.status === 'retry' &&
                                    <p className="cargando">Vuelve a intentarlo</p>
                                }
                            </div>

                        </div>


                        <div>
                            <label htmlFor="operator">¿CUÁL ES TU OPERADOR DE TELEFONÍA MÓVIL?</label> <br />
                            <select name="operator" id="operador" ref={this.operatorRef} onChange={this.changeState}>

                                <option value={this.props.operator}>{this.props.operator}</option>

                            </select>
                        </div>
                        {this.validator.message('operator', this.state.codigo.operator, 'required')}

                        <input type="submit" value="INGRESAR" />

                        <div>
                            <img className="mensaje" src={mensaje} alt="icono" /> <br />
                            <a rel="noreferrer" target="_blank" href="https://movistar-formularios-dot-modified-wonder-87620.appspot.com/estereo-picnic/"> <img className="iconowa" src={botonayudacodigo} alt="mensaje" onMouseOut={this.showWhatsapp} onMouseOver={this.showWhatsapp} /></a> <br />
                            {/* <img className="horario" src={horario_to} alt="icono" /> <br /> */}
                        </div>
                    </form>


                </div>
                {/* MENSAJE CUANDO SEA EXITOSO EL CÓDIGO */}
                <div className="success_form">
                    <img src={iconocheck} alt="check" />
                    <p>{this.state.codefinal}</p>
                    Tu código ha sido validado correctamente.
                    Podrás usarlo del 11 al 13 de diciembre en
                    esta misma página para acceder al
                    contenido VIP del festival.
                    <div id="tienda_fep_valid" className="acceder_tienda">
                        <a id="tienda_fep_valid" target="_blank " rel="noreferrer" href="https://ecommerce3.entradasamarillas.com/">Ir a la tienda</a>
                    </div>
                </div>

                {/* MENSAJE CUANDO SEA EXITOSO EL CÓDIGO */}
                <div className="failed_form">
                    <p>{this.state.codefinal}</p>
                    {this.state.message}
                    <img src={iconoerror} alt="error" />
                    <div onClick={this.returnForm} className="acceder_tienda">REINTENTAR</div>
                </div>
            </section>
        )
    }
}

export default Formulario2;